import React from "react";
import { Card, Table, Row, Col } from 'antd';

class Peliculas extends React.Component {
  render() {
    const { Meta } = Card;

    const dataSource = [
      {
        key: '1',
        titulo: 'El Seor de los Anillos - Las Dos Torres',
        ao: 2002,
        coste: '94 millones de USD$',
        beneficios: '947,5 millones de USD$'
      },
      {
        key: '2',
        titulo: 'Forrest Gump',
        ao: 1994,
        coste: '55 millones de USD$',
        beneficios: '678,2 millones de USD$'
      },
      {
        key: '3',
        titulo: 'El Club de la Lucha',
        ao: 1999,
        coste: '63 millones de USD$',
        beneficios: '101,2 millones de USD$'
      },
    ];

    const columns = [
      {
        title: 'Pelcula',
        dataIndex: 'titulo',
        key: 'titulo',
      },
      {
        title: 'Ao',
        dataIndex: 'ao',
        key: 'ao',
      },
      {
        title: 'Coste',
        dataIndex: 'coste',
        key: 'coste',
      },
      {
        title: 'Beneficios',
        dataIndex: 'beneficios',
        key: 'beneficios',
      },
    ];

    return (<Row justify='center' gutter={[16, 16]}>
      <Col span={6}><Card
        title="El Seor de los Anillos - Las Dos Torres" cover={<img style={{maxHeight: "37em"}} src="lotr.jpg" alt="Portada El Seor de los Anillos - Las Dos Torres" />}
      >
      </Card></Col>
      <Col span={6}><Card
        title="Forrest Gump" cover={<img style={{maxHeight: "37em"}} src="gump.jpg" alt="Portada de Forrest Gump" />}
      >
      </Card></Col>
      <Col span={6}><Card
        title="El Club de la Lucha" cover={<img style={{maxHeight: "37em"}} src="lucha.jpg" alt="Portada de El Club de la Lucha" />}
      >
      </Card></Col>
      <Col span={12}><Table dataSource={dataSource} columns={columns} />;</Col>
    </Row>);
  }
};
export default Peliculas;