import { LikeOutlined, HomeOutlined, PlayCircleOutlined, VideoCameraOutlined } from '@ant-design/icons';
import { Route, Routes, Link, BrowserRouter as Router } from "react-router-dom";
import { Menu, Row, Col, Layout } from 'antd';
import SobreMi from "./SobreMi";
import Musica from "./Musica";
import Peliculas from "./Peliculas";
import Aficiones from "./Aficiones";
import React from "react";
import "./style.css";


class App extends React.Component {

  render() {
    const { Footer } = Layout;
    return <main>
      <Router>
        <Row gutter={[24, 24]}>
          <Col span={24}>
            <Menu mode="horizontal" items={[
              {
                label: <Link to="/web">Sobre Mi</Link>,
                key: 'sobreMi',
                icon: <HomeOutlined />,
              },
              {
                label: <Link to="/aficiones">Aficiones</Link>,
                key: 'aficiones',
                icon: <LikeOutlined />
              },
              {
                label: <Link to="/musica">Msica</Link>,
                key: 'musica',
                icon: <PlayCircleOutlined />
              },
              {
                label: <Link to="/peliculas">Pelculas</Link>,
                key: 'peliculas',
                icon: <VideoCameraOutlined />
              },
            ]} />
          </Col>
          <Routes>

            <Route path="/web" element={
              <SobreMi />
            } />
            <Route path="/aficiones" element={
              <Aficiones />
            } />
            <Route path="/musica" element={
              <Musica />
            } />
            <Route path="/peliculas" element={
              <Peliculas />
            } />
          </Routes>
          <Col span={24}>
            <Footer style={{ textAlign: 'center' }}> Marcos Fernndez Gonzlez | MIW | 2022/2023 </Footer>
          </Col>
        </Row>
      </Router>

    </main>
      ;
  }
};
export default App;